<template>
  <div class="last-order-time-warn" v-if="isRestaurant && isGoingToClose">
    {{ $t("lastOrderIn", { minute: remainingTime }) }}
  </div>
</template>
<i18n>
{
  "en": {
    "lastOrderIn": "Last order in {minute} minutes"
  },
  "is": {
    "lastOrderIn": "SÃ­Ã°asta pÃ¶ntun eftir {minute} mÃ­nÃºtur"
  }
}
</i18n>
<script>
import openingHours from "@/mixins/openingHours";
import get from "@/util/get";
import getNextOpenStatusChange from "@/util/getNextOpenStatusChange";

const debugMode = false;

export default {
  name: "LastOderTimeWarning",
  mixins: [openingHours],
  props: {
    id: Number,
  },
  data() {
    return {
      remainingTime: 30,
      isGoingToClose: false,
    };
  },
  computed: {
    isRestaurant() {
      return get(this.merchant, "attribute_set_name") === "Restaurants";
    },
    nextStatusChange() {
      const { openingHours } = this;
      return getNextOpenStatusChange({ openingHours });
    },
  },
  methods: {
    calIsGoingToClose() {
      const { merchant, nextStatusChange } = this;
      const { isOpen, nextChangeDate } = nextStatusChange;

      if (!isOpen) return (this.isGoingToClose = false);

      const lastTimeToOrder =
        +nextChangeDate - get(merchant, "last_order_time", 30) * 60000;

      // if (debugMode) {
      //   const fakeDate = lastTimeToOrder - 60000;

      //   console.log("calIsGoingToClose", {
      //     fakeDate: fakeDate,
      //     result:
      //       +lastTimeToOrder - 30 * 60000 <= fakeDate &&
      //       fakeDate < +lastTimeToOrder,
      //   });
      //   return (this.isGoingToClose =
      //     +lastTimeToOrder - 30 * 60000 <= fakeDate &&
      //     fakeDate < +lastTimeToOrder);
      // }

      return (this.isGoingToClose =
        +lastTimeToOrder - 30 * 60000 <= +new Date() &&
        +new Date() < +lastTimeToOrder);
    },
    calTimeRemainingBeforeClose() {
      if (!this.merchant) return;
      this.calIsGoingToClose();

      const storeLastOrderTimeInMs =
        get(this.merchant, "last_order_time", 30) * 60000;
      const { nextChangeDate } = this.nextStatusChange;
      const remainingTimeInMs =
        +nextChangeDate - (+new Date() + storeLastOrderTimeInMs);

      this.remainingTime = Math.floor(Math.max(0, remainingTimeInMs / 60000));
    },
  },
  mounted() {
    this.calTimeRemainingBeforeClose();
    this.timeRemainingTimer = setInterval(
      this.calTimeRemainingBeforeClose,
      debugMode ? 3000 : 60000
    );
  },
  beforeDestroy() {
    clearInterval(this.timeRemainingTimer);
  },
  watch: {
    merchant() {
      this.calTimeRemainingBeforeClose();
    },
  },
};
</script>

