import merchant from "./merchant";
import { extractOpenHourData } from "@/util/openingHours";

export default {
  name: "openingHours",
  mixins: [merchant],
  computed: {
    openingHours() {
      const { restaurant_opening_time = "", restaurant_open_date = "" } =
        this.merchant || {};

      return extractOpenHourData({
        restaurant_opening_time,
        restaurant_open_date,
      });
    },
  },
};
