import { nDaysAhead, filterByDay } from "@/util/openingHours";
import pipe from "@/util/pipe";
import CloudinaryQuery from "@/util/CloudinaryQuery";

export default {
  name: "VMixAhaGridItem",
  props: {
    title: String,
    subtitle: String,
    status: Number,
    image: String,
    entity_id: Number,
    sku: String,
    rating: Number,
    url: String,
    open_today: String,
    open_tomorrow: String,
    open_days: String,
  },
  computed: {
    cloudinaryImage: function() {
      if (this.image === "no_selection" || !this.image) return "";
      return new CloudinaryQuery(this.image).trim('').height(230).toURL();
    },
    ratingStyle: function() {
      return "width: " + this.rating + "%;";
    },
    getUrl: function() {
      return "https://www.aha.is/" + this.url;
    },
    openingHours: function() {
      let openingHours =
        this.open_days &&
        this.open_days
          .split(";")
          .filter(Boolean)
          .map(day => day.split(","));
      if (openingHours.every(date => date.includes("closed"))) {
        return;
      }
      let openDate,
        dayIndex = 0,
        openAt;

      while (!openAt || (openAt && openAt[1] === "closed")) {
        openDate = nDaysAhead(dayIndex);
        openAt = pipe(
          filterByDay(openDate),
          ([first] = []) => first
        )(openingHours);
        dayIndex++;
      }

      let data = pipe(
        filterByDay(openDate),
        ([[, start, end] = []]) => {
          return `${start} - ${end}`;
        }
      )(openingHours);
      return data;
    },
  },
};
