const weekdays = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
const weekDayIndexMap = weekdays.reduce((prevResult, currDayStr, currIndex) => {
  prevResult[currDayStr] = currIndex;
  return prevResult;
}, {});

function getSortedOpeningHours({ openingHours }) {
  return (
    JSON.parse(JSON.stringify(openingHours))
      // .filter(Boolean)
      // .map(day => day.split(","))
      .sort(([dayStrA, openStrA], [dayStrB, openStrB]) => {
        const compareDayStr =
          weekDayIndexMap[dayStrA] - weekDayIndexMap[dayStrB];
        if (compareDayStr !== 0) return compareDayStr;

        return getDecimalHours(openStrA) - getDecimalHours(openStrB);
      })
  );
}

function preventOverlapInOneDay({ openingHours, dayStr }) {
  const sortedOpeningHours = getSortedOpeningHours({
    openingHours,
  });
  const selectedDayOpenHours = filterByDay(dayStr)(sortedOpeningHours).reduce(
    (result, currentOpenHour, index) => {
      const [, currOpenTime, currCloseTime] = currentOpenHour;
      if (index === 0) {
        return [...result, currentOpenHour];
      }

      const [, , prevCloseTime] = result[result.length - 1];
      if (prevCloseTime === currOpenTime) {
        // if overlap in one day
        // ["Thu", "00:00", "12:00"]
        // ["Thu", "12:00", "24:00"]
        // it should become ["Thu", "00:00", "24:00"]
        result[result.length - 1][2] = currCloseTime;
        return result;
      } else {
        return [...result, currentOpenHour];
      }
    },
    []
  );
  return selectedDayOpenHours;
}

function getSortedPreventOverlapHours({ openingHours }) {
  const sortedOpeningHours = getSortedOpeningHours({
    openingHours,
  });

  const preventOverlapOpenHours = getSortedOpeningHours({
    openingHours: weekdays.reduce((result, currDayStr) => {
      return [
        ...result,
        ...preventOverlapInOneDay({
          openingHours: sortedOpeningHours,
          dayStr: currDayStr,
        }),
      ];
    }, []),
  });
  return preventOverlapOpenHours;
}

function getNextOpenTime({ openingHours, selectedDayStr }) {
  const preventOverlapOpenHours = getSortedPreventOverlapHours({
    openingHours,
  });

  const selectedDayOpenHour =
    filterByDay(selectedDayStr)(preventOverlapOpenHours) || [];
  const willOpenOnSelectedDay =
    selectedDayOpenHour.length &&
    selectedDayOpenHour.every(([, openTime]) => openTime !== "closed");
  if (willOpenOnSelectedDay) {
    return {
      selectedDayOpenHour,
      nextOpenTimeStr: selectedDayOpenHour[0][1],
      nextOpenDayStr: selectedDayOpenHour[0][0],
    };
  }

  const dayStrIndex = preventOverlapOpenHours.findIndex(
    ([day]) => day === selectedDayStr
  );
  const newSortedOpeningHours = [
    ...preventOverlapOpenHours.slice(dayStrIndex),
    ...preventOverlapOpenHours.slice(0, dayStrIndex),
  ].filter(([dayStr]) => dayStr !== selectedDayStr);

  let nextOpenTimeStr = null;
  let nextOpenDayStr = null;
  for (let i = 0; i < newSortedOpeningHours.length; i++) {
    const [datStr, openTime, closeTime] = newSortedOpeningHours[i];
    if (openTime !== "closed") {
      nextOpenTimeStr = openTime;
      nextOpenDayStr = datStr;
      break;
    }
  }
  return {
    selectedDayOpenHour,
    nextOpenTimeStr,
    nextOpenDayStr,
  };
}

// store should be is opening
function getNextCloseTime({ openingHours }) {
  const todayStr = nDaysAhead(0);

  const preventOverlapOpenHours = getSortedPreventOverlapHours({
    openingHours,
  });

  const dayStrIndex = preventOverlapOpenHours.findIndex(
    ([day]) => day === todayStr
  );
  const newSortedOpeningHours = [
    ...preventOverlapOpenHours.slice(dayStrIndex),
    ...preventOverlapOpenHours.slice(0, dayStrIndex),
  ];

  let nextCloseTimeStr = null;
  let nextCloseDayStr = null;

  const isEqualTimeStr = (str1, str2) => {
    return (
      str1 === str2 ||
      (str1 === "00:00" && str2 === "24:00") ||
      (str1 === "24:00" && str2 === "00:00")
    );
  };
  for (let i = 0; i < newSortedOpeningHours.length - 1; i++) {
    const [currDayStr, currOpenTime, currCloseTime] = newSortedOpeningHours[i];
    const [nextDayStr, nextOpenTime, nextCloseTime] = newSortedOpeningHours[
      i + 1
    ];
    if (
      i === newSortedOpeningHours.length - 2 &&
      !isEqualTimeStr(nextCloseTime, newSortedOpeningHours[0][1])
    ) {
      // last item check;
      nextCloseTimeStr = nextCloseTime;
      nextCloseDayStr = nextDayStr;
    }
    if (isEqualTimeStr(currCloseTime, nextOpenTime)) {
      continue;
    }
    if (!isEqualTimeStr(currCloseTime, nextOpenTime)) {
      nextCloseTimeStr = currCloseTime;
      nextCloseDayStr = currDayStr;
      break;
    }
  }

  return {
    nextCloseTimeStr,
    nextCloseDayStr,
  };
}

function getNextAccurateDateFromNow({ dayStr, timeStr } = {}) {
  if (!dayStr || !timeStr) return null;
  const todayIndex = weekDayIndexMap[nDaysAhead(0)];
  const selectedDayIndex = weekDayIndexMap[dayStr];

  const diffDays =
    selectedDayIndex >= todayIndex
      ? selectedDayIndex - todayIndex
      : 7 - todayIndex + selectedDayIndex;

  return new Date(+convertTimeStrToDate(timeStr) + diffDays * 86400000);
}

function checkTodayOpenStatus({ openingHours }) {
  const todayStr = nDaysAhead(0);

  const todayOpeningHour = getSortedPreventOverlapHours({
    openingHours: filterByDay(todayStr)(openingHours) || [],
  });

  const currentOpenTime =
    todayOpeningHour.length &&
    todayOpeningHour.find(
      ([, openTime, closeTime]) =>
        getDecimalHours(openTime) <= decimalHoursNow() &&
        decimalHoursNow() < getDecimalHours(closeTime)
    );

  const isOpenToday =
    !!todayOpeningHour.length &&
    todayOpeningHour.every(([, openTime]) => openTime !== "closed");
  const isOpen = isOpenToday && !!currentOpenTime;

  const lastTodayCloseTime = get(
    todayOpeningHour,
    `${todayOpeningHour.length - 1}.2`
  );
  const willOpenToday =
    isOpenToday &&
    !isOpen &&
    decimalHoursNow() < getDecimalHours(lastTodayCloseTime);
  return {
    todayStr,
    todayOpeningHour,
    currentOpenTime,
    isOpenToday,
    lastTodayCloseTime,
    willOpenToday,
    isOpen,
  };
}

export default function getNextOpenStatusChange({ openingHours = [] } = {}) {
  const result = {
    isOpen: false,
    isOpenToday: false,
    willOpenToday: false,
    nextChangeTimeStr: null,
    nextChangeDayStr: null,
    nextChangeDate: null,
    todayOpeningHour: null,
  };
  const setNextChange = ({ dayStr, timeStr }) => {
    result.nextChangeTimeStr = timeStr;
    result.nextChangeDayStr = dayStr;
    result.nextChangeDate = getNextAccurateDateFromNow({ dayStr, timeStr });
  };

  if (openingHours.every(date => date.includes("closed"))) {
    return result;
  }

  const {
    todayStr,
    todayOpeningHour,
    currentOpenTime,
    isOpenToday,
    lastTodayCloseTime,
    willOpenToday,
    isOpen,
  } = checkTodayOpenStatus({ openingHours });

  result.isOpenToday = isOpenToday;
  result.isOpen = isOpen;
  result.willOpenToday = willOpenToday;
  result.todayOpeningHour = todayOpeningHour;

  if (willOpenToday) {
    const [, willOpenTodayTime] =
      todayOpeningHour.find(
        ([, openTime]) => decimalHoursNow() < getDecimalHours(openTime)
      ) || [];

    setNextChange({ dayStr: todayStr, timeStr: willOpenTodayTime });
    return result;
  }
  if (isOpen) {
    // const [nextCloseDayStr, openTime, nextCloseTimeStr] = currentOpenTime;
    const { nextCloseTimeStr, nextCloseDayStr } = getNextCloseTime({
      openingHours,
      selectedDayStr: todayStr,
    });

    setNextChange({ dayStr: nextCloseDayStr, timeStr: nextCloseTimeStr });

    return result;
  }

  const { nextOpenTimeStr, nextOpenDayStr } = getNextOpenTime({
    openingHours,
    selectedDayStr: todayStr,
  });

  setNextChange({ dayStr: nextOpenDayStr, timeStr: nextOpenTimeStr });

  return result;
}
