<template>
  <div class="timer pull-right display-table">
    <div
      v-for="(n, unit) in timeRemaining"
      :key="unit"
      :class="['display-table-cell', type === 'grid' && 'white']"
    >
      <span class="value">{{ n }}</span>
      <span class="label">{{ $t(unit) }}</span>
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "days": "Dagar",
    "hours": "Klst",
    "minutes": "MÃ­n",
    "seconds": "Sek"
  },
  "en": {
    "days": "Days",
    "hours": "Hours",
    "minutes": "Min",
    "seconds": "Sec"
  }
}
</i18n>

<script>
export default {
  name: "DealTimer",
  data() {
    return {
      timeRemaining: {},
      timer: "",
    };
  },
  props: {
    special_to_date: [Date, String],
    type: { type: String, default: "details" },
  },
  methods: {
    // a and b are javascript Date objects
    dateDifference(a, b) {
      const msPerDay = 1000 * 60 * 60 * 24;
      // Discard the time and time-zone information.
      const utc1 = Date.UTC(a.getFullYear(), a.getMonth(), a.getDate());
      const utc2 = Date.UTC(b.getFullYear(), b.getMonth(), b.getDate());
      const diff = new Date(b - a);
      return {
        days: Math.floor((utc2 - utc1) / msPerDay),
        hours: diff.getHours(),
        minutes: diff.getMinutes(),
        seconds: diff.getSeconds(),
      };
    },
    updateTimeRemaining() {
      const { dateDifference, special_to_date } = this;
      this.timeRemaining = dateDifference(
        new Date(),
        new Date(special_to_date)
      );

      this.$emit("timeleft", this.timeLeft);
    },
  },
  computed: {
    timeLeft() {
      const { days, hours } = this.timeRemaining;
      const timeLeft = (
        n,
        unit,
        plural = "s",
        singular = "",
        left = "left"
      ) => {
        return `${n} ${unit}${n > 1 ? plural : singular} ${left}`;
      };
      switch (this.$root.$i18n.locale) {
        case "en":
          return days ? timeLeft(days, "day") : timeLeft(hours, "hour");
        default:
          return days
            ? timeLeft(days, "dag", "ar", "ur", "eftir")
            : timeLeft(hours, "klst.", "", "", "eftir");
      }
    },
  },
  created() {
    const eventDate = new Date(this.special_to_date);
    const currentDate = new Date();
    if (eventDate < currentDate) {
      this.timeRemaining = {
        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0,
      };
      return;
    }
    this.timer = setInterval(this.updateTimeRemaining, 1000);
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
};
</script>

