<script>
import GridItemMixin from "@/mixins/GridItemMixin.js";
import BaseButton from "@/components/BaseButton";
import ImageViewer from "@/components/ImageViewer";
import ItemGridPlaceholder from "@/components/ItemGridPlaceholder";
import LastOderTimeWarning from "@/components/LastOderTimeWarning";

// import deliveryTime from "../assets/images/delivery-time.png";

export default {
  name: "GridRestaurantItem",
  mixins: [GridItemMixin],
  components: {
    BaseButton,
    ImageViewer,
    ItemGridPlaceholder,
    LastOderTimeWarning,
  },
  props: {
    useSkeleton: Boolean,
    entity_id: undefined,
    delivery_price: Number,
    prep_time: Number,
    delivery_time: Number,
    open_time: String,
    rating: Number,
    reviews_count: Number,
    last_order_time: Number,
    open_days: String,
    url: {
      type: String,
      default: "",
    },
  },
  computed: {
    deliveryTime() {
      return require("@/assets/images/delivery-time.png");
    },
    showSkeleton() {
      // return this.useSkeleton;
      return this.useSkeleton && !this.entity_id;
    },
    redirectUrl() {
      return this.url || "";
    },
    timeleft: function() {
      return (
        parseInt(
          (this.special_to_date.getTime() - new Date().getTime()) /
            (60 * 60 * 1000)
        ) + " klst eftir."
      );
    },
    delivery_text: function() {
      let lower = Math.ceil((this.delivery_time + this.prep_time) / 10) * 10;
      return lower + " - " + (lower + 15);
    },
  },
};
</script>

