<template>
  <section class="items-grid clearfix">
    <LoadingWrapper
      class="loading-wrapper--my-70"
      :loadingKeys="[useLoadingIndicator && LOADING_KEY_FETCH_ITEM_GRID]"
    >
      <div v-if="useSkeleton ? true : dataReady" class="container">
        <h2 class="items-grid__title">
          <router-link :to="titleLink">{{ title }}</router-link>
        </h2>
        <div class="items-grid__top clearfix">
          <p class="items-grid__subtitle">{{ subTitle }}</p>
          <router-link
            v-show="titleLink"
            :to="titleLink"
            class="items-grid__view-all"
            >{{ titleLinkText }}</router-link
          >
        </div>
        <div class="items-grid__section">
          <SwipperButtons>
            <DraggableScroll
              v-bind:class="{
                row: true,
                'hide-scroll-bar': true,
                'items-grid__wrap': showCarousel,
              }"
            >
              <component
                v-for="item in itemsToRender"
                v-bind="item"
                useSkeleton
                :key="item && item.entity_id"
                :is="componentName"
              />
              <GridItemSeeMore v-if="showMore" :linkTo="titleLink" />
            </DraggableScroll>
          </SwipperButtons>
        </div>
      </div>
    </LoadingWrapper>
  </section>
</template>

<script>
import GridDealItem from "./GridDealItem.vue";
import GridRestaurantItem from "./GridRestaurantItem.vue";
import GridStoreItem from "./GridStoreItem.vue";
import GridGroceryItem from "./GridGroceryItem.vue";
import GridItemSeeMore from "./GridItemSeeMore";
import LoadingWrapper from "@/components/LoadingWrapper";
import DraggableScroll from "@/components/DraggableScroll";
import ItemGridPlaceholder from "@/components/ItemGridPlaceholder";
import fetchItems from "@/util/fetchItems";
import execWithLoading from "@/util/execWithLoading";
import { mapGetters } from "vuex";
import { LOADING_KEY_FETCH_ITEM_GRID } from "@/store/loadingModule";
import SwipperButtons from "@/components/SwipperButtons";

export default {
  name: "ItemsGrid",
  components: {
    GridDealItem,
    GridRestaurantItem,
    GridStoreItem,
    GridGroceryItem,
    GridItemSeeMore,
    LoadingWrapper,
    ItemGridPlaceholder,
    DraggableScroll,
    SwipperButtons,
  },
  props: {
    title: String,
    subTitle: String,
    titleLinkText: String,
    titleLink: { type: String, default: "" },
    type: String,
    url: String,
    limit: { type: Number, default: 3 },
    showCarousel: { type: Boolean, default: true },
    showMore: Boolean,
    useLoadingIndicator: Boolean,
    useSkeleton: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      items: [],
      LOADING_KEY_FETCH_ITEM_GRID,
    };
  },
  computed: {
    ...mapGetters(["userAddress", "toggleLoading"]),
    itemsToRender() {
      return this.items.length ? this.items : [...Array(this.limit)];
    },
    componentName: function() {
      return "grid-" + this.type + "-item";
    },
    dataReady: function() {
      return this.items.length > 0;
    },
  },
  methods: {
    async fetchData(userAddress = this.userAddress) {
      let types = {
        // TODO: Fix (lower) cache when going live
        deal: {
          data: { service: "deals", limit: this.limit },
        },
        store: {
          data: {
            service: "seller_list",
            type: "Store",
            limit: this.limit,
            cache: 600,
            user_postcode: userAddress.postcode,
            user_lat: userAddress.lat,
            user_lon: userAddress.lon,
          },
        },
        restaurant: {
          data: {
            service: "seller_list",
            type: "Restaurants",
            limit: this.limit,
            cache: 300,
            user_postcode: userAddress.postcode,
            user_lat: userAddress.lat,
            user_lon: userAddress.lon,
          },
        },
        grocery: {
          data: { service: "store_frontpage", store_id: 54263, cache: 600 },
        },
        featured: {
          url:
            "https://www.aha.is/pos/report.php?r=20&u1=Restaurants&m=1&h=8fcdac9d7b7939447641cd79bd25dc6d9bb2e8cb&export=json&json_force_object=1",
        },
      };
      let self = this;
      this.items = [];
      const res = await execWithLoading(LOADING_KEY_FETCH_ITEM_GRID, () =>
        fetchItems(types[this.type].data, this.url)
      );

      let data = res.data[0];
      for (var x in data) {
        data[x].special_from_date = new Date(data[x].special_from_date);
        data[x].special_to_date = new Date(data[x].special_to_date);
        self.items.push(data[x]);
      }
    },
  },
  async mounted() {
    await this.fetchData();
  },
  watch: {
    userAddress(newAddress) {
      this.fetchData(newAddress);
    },
  },
};
</script>

